export const PM_THRESHOLDS = {
  PM01: {
    Good: 0.0,
   'Warning for sensitive groups': 0.5,
    Warning: 1.0,
    'Effect on health': 2.0,
    Hazardous: Number.MAX_VALUE
  },
  PM25: {
    Good: 15.0,
    'Warning for sensitive groups': 25.0,
    Warning: 37.5,
    'Effect on health': 75.0,
    Hazardous: Number.MAX_VALUE
  },
  PM100: {
    Good: 50.0,
    'Warning for sensitive groups': 80.0,
    moderate: 120.0,
    'Effect on health': 180.0,
    Hazardous: Number.MAX_VALUE
  }
};

const determineAirQuality = (pm01, pm25, pm100) => {
  // Check Excellent
  if (pm01 <= PM_THRESHOLDS.PM01.Good &&
    pm25 <= PM_THRESHOLDS.PM25.Good &&
    pm100 <= PM_THRESHOLDS.PM100.Good) {
    return "Good";
  }
  // Check Good
  else if (pm01 <= PM_THRESHOLDS.PM01.Warningforsensitive &&
    pm25 <= PM_THRESHOLDS.PM25.Warningforsensitive &&
    pm100 <= PM_THRESHOLDS.PM100.Warningforsensitive) {
    return "Warning for sensitive";
  }
  // Check Moderate
  else if (pm01 <= PM_THRESHOLDS.PM01.Warning &&
    pm25 <= PM_THRESHOLDS.PM25.Warning &&
    pm100 <= PM_THRESHOLDS.PM100.Warning) {
    return "Warning";
  }
    // Check Effecthealth
  else if (pm01 <= PM_THRESHOLDS.PM01.Effecthealth &&
    pm25 <= PM_THRESHOLDS.PM25.Effecthealth &&
    pm100 <= PM_THRESHOLDS.PM100.Effecthealth) {
    return "Effect on health";
  }
  // If above all thresholds, return Hazardous
  else {
    return "Hazardous";
  }
};

